.Stcontainer{
    border-bottom: 1px solid rgba(255,255,255,0.3);
    background-color: transparent;
    transition: all 0.3s;
    margin: 0 auto;
   
}
.fixedBox{
    width: 100%;
    position: fixed;
    background-color: #fff;
    border-bottom: 1px solid #eee;
    z-index: 1000;

}
.fixedBox1{
    color: #000;
    height: 85px;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    box-sizing: border-box;
    line-height: 85px;
    position: relative;
}
.fixedLogo{
    width: 101px;
    height: 42px;
    background: url("//contents-image.twayair.com/homepage/images/common/top_logo.png");
    cursor: pointer;
}
.fixedMyPage{
    text-indent: -9999px;
    width: 30px;
    height: 30px;
    background:url("//contents-image.twayair.com/homepage/images/ico/ico_top_login.png") 100% 0px no-repeat
}
.Stcontainer:hover{
    background-color: #fff;
    border-bottom: 1px solid #eee;
}
.Stcontainer:hover .StNavList{
    color: #000;
}
.Stcontainer:hover .StMyPage{
    background: url("//contents-image.twayair.com/homepage/images/ico/ico_top_login.png") no-repeat 100% 0;
}
.Stcontainer:hover .StLogo{
    background: url('//contents-image.twayair.com/homepage/images/common/top_logo.png');
}
.StLogo{
    width: 101px;
    height: 42px;
    background: url("//contents-image.twayair.com/homepage/images/main/top_logo.png") no-repeat 0 0;
    cursor: pointer;
}
.StNavBox{
    width: 820px;
    margin-left:60px;
    box-sizing: border-box;
}
.StNavList{
    height: 85px;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    box-sizing: border-box;
    line-height: 85px;
    color: #fff;
    position: relative;
    cursor: pointer;
}

.StNavList:hover.StNavList::after{
 width: 100%;
}
.StNavList::after{
    content: '';
    display: block;
    width: 0%;
    height: 4px;
    background-color: #f00;
    position: absolute;
    left: 50%;
    bottom: 0;
    transition: all 0.3s;
   transform: translateX(-50%);
}
.StNavContainer{
    display: flex;
    list-style: none;
    justify-content: space-between;
    align-items: center;
}
.StHeaderContainer{
    width: 1200px;
    margin:  0 auto;
    height: 85px;
    justify-content: space-between;
    display: flex;
    align-items: center;
}
.StSideBox{
    display: flex;
    gap:10px
}
.StMyPage{
    text-indent: -9999px;
    width: 30px;
    height: 30px;
    background:url("//contents-image.twayair.com/homepage/images/main/ico_top_login.png") no-repeat 100% 0 ;
    cursor: pointer;
}

.StMyPage2{
    text-indent: -9999px;
    width: 30px;
    height: 30px;
    background:url("//contents-image.twayair.com/homepage/images/main/ico_top_login_on.png") 100% 0px no-repeat;
    cursor: pointer;
}
.Stcontainer:hover .StMyPage2{
    background: url("//contents-image.twayair.com/homepage/images/ico/ico_top_login_on.png") 100% 0px no-repeat
}
.fixedMyPage2{
    cursor: pointer;
    text-indent: -9999px;
    width: 30px;
    height: 30px;
    background:url("//contents-image.twayair.com/homepage/images/ico/ico_top_login_on.png")100% 0px no-repeat
}